}

.App-content-view {
  padding-top: 15px;
  padding-bottom: 15px;
  background-color: #282c34;
}

@media screen and (min-width: 980px) {
  .card-grid-container {
    columns: 3;
  }
}

@media screen and (min-width: 635px) and (max-width: 979px) {
  .card-grid-container {
    columns: 2;
  }
}

@media screen and (min-width: 600px) and (max-width: 634px) {
  .card-grid-container {
    columns: 1;
  }
}

@keyframes fadeInAnimation {
  0% {
      opacity: 0;
  }
  100% {
      opacity: 1;
   }
}

@keyframes fadeOutIn {
  0% {
    opacity: 0.5;
  }
  25% {
    opacity: 0;
  }
  50% {
    opacity: 0.3;
  }
  75% {
    opacity: 0.5;
  }
  100% {
    opacity: 1;
  }
}

@keyframes slideInWithAnim {
  0% {
      opacity: 0;
      transform: translateX(-100px);
  }
  100% {
      opacity: 1;
      transform: translateX(0px);
  }
}